
.resource-bar {
    position: relative;
    height: 100%;

    border-radius: 3px;

    background-color: rgb(145, 162, 169);
}
.resource-bar .filling {
    position: absolute;
    height: 100%;
    width: 55px;
    
    padding: 0px;
    top: 0px;
    left: 0px;

    border-radius: 3px;
}
.resource-bar .number {
    color: white;
    text-shadow:
    -1px -1px 0 #000,  
     1px -1px 0 #000,
     -1px 1px 0 #000,
      1px 1px 0 #000;
    
    z-index: var(--z-overlay);

    font-size: 1.5rem;
    line-height: 2rem;
    font-family: HomeFont;
    text-align: center;

    margin: 0px;
    padding: 0px;
    width: 100%;

    position: absolute;
    left: 0px;
    bottom: 0rem;
    
}