
:root {
    --nav-height: 80px;
    --nav-item-padding: 40px;
    --nav-font-size: var(--text-size);
    --nav-portrait-font-size: calc(1.25 * var(--nav-font-size));

    --nav-background-color: var(--dark-gray-color);
    --nav-hover-color: var(--theme-color-1-light);

    --subnav-height: 50px;
    --subnav-item-padding-left: 40px;
    --subnav-item-padding-right: 20px;
    --subnav-background-color: rgba(15, 15, 15, 1);

    --subnav-section-max-width: 225px;
    --subnav-section-min-width: 90px;

    --nav-z-mega-dropdown: 999;
    --nav-z-item: 9999;
    --undernav-padding: 15px;
    --undernav-height: calc(var(--banner-3-height) + var(--undernav-padding) * 2);
}

#Navigation-Section {
    position: fixed;
    height: var(--nav-height);
    width: 100%;
    z-index: 99999999;
    background-color: var(--nav-background-color);

    top: 0px;
}
#Navigation-Section * {
    font-family: HomeFont;
}



nav {
    width: 100%;
    height: var(--nav-height);
    margin: 0px;
    padding: 0px;
    background-color: var(--nav-background-color);
}
.nav-landscape {
    width: 100%;
    height: var(--nav-height);

    display: flex;
    gap: var(--nav-item-padding);

    padding-left: var(--content-margin-left);
}
.nav-item {
    height: 100%;
    z-index: var(--nav-z-item) !important;
    display: inline-block;            /* Ensures correct height as 100% of parent */

    font-size: var(--nav-font-size);
    line-height: var(--nav-height);   /* Ensures text is centered vertically */
    letter-spacing: 0.04em;
    text-align: center;
    text-decoration: none;
    color: white;
    
    cursor: pointer;
}
.nav-item:hover {
    color: orange;
}
.nav-item .inline-icon {
    display: inline-block;
    line-height: var(--nav-height);
}
.nav-item.login-button {
    display: flex;
}
.nav-login-buttons {
    display: flex;
    gap: 20px;
    
    margin-left: auto;
    padding-right: 5vw;
    
    color: white;
}
.nav-simple-checkbox {
    margin-top: 27px;
    margin-right: 5px;
    height: 25px;
    width: 25px;
}
.nav-item > a {
    display: inline-block;
    color: white;
    height: 100%;
    width: 100%;
}
.nav-item a:hover {
    color: orange;
}
.nav-link {
    cursor: pointer;
}


/* ------------- Landscape ------------- */
.nav-landscape > .nav-link {
    transition-property: padding-left;
    transition-duration: var(--transition-duration);
}
.nav-landscape > .nav-link {
    padding-left: 5px;
}
.nav-landscape > .nav-link,
.nav-top-item > * {
    height: var(--nav-height);
    color: white;
    line-height: var(--nav-height);
}

.nav-landscape-top-link:hover,
.nav-landscape-top-link .nav-link:hover,
.subnav-section .nav-link:hover,
.nav-top-item .nav-link:hover,
.nav-landscape > .nav-link:hover {
    color: var(--theme-color-1-light);
}

.nav-landscape > *,
.subnav-section .nav-link,
.subnav-section h4 {
    font-weight: normal;
    color: white;
    font-size: var(--nav-font-size);
    line-height: var(--nav-font-size);
}
.subnav-section h4 {
    margin-top: 1rem;
    margin-bottom: 0.5rem;
}
.subnav-section .nav-link {
    color: rgb(190, 190, 190);
}

.nav-link.disabled {
    color: rgb(90, 90, 90) !important;
}

.subnav {
    background-color: black;

    width: 100%;
    padding-left: var(--content-margin-left);
    padding-top: 0.5rem;
    padding-bottom: var(--nav-item-padding);

    display: flex;
    gap: 3rem;

    border-top: solid var(--theme-color-1-light) 2px;
}

.subnav-section {
    max-width: var(--subnav-section-max-width);
    min-width: var(--subnav-section-min-width);
}
.subnav-section-item {
    margin-top: 0.75rem;
}
.subnav-section-item .inline-icon {
    padding: 0px;
    margin: 0px;
    margin-right: 0.25rem;

    margin-top: calc(var(--nav-font-size) * 0.1);
    height: calc(var(--nav-font-size) * 0.8);
}




/* ------------- Portrait ------------- */
.nav-drawer {
    width: 100%;
    min-height: 100vh;
    
    background-color: black;
    color: white !important;
    
    padding: 2rem;
    padding-top: 4rem;
}
.nav-drawer * {
    font-weight: normal;
    color: white;
    font-size: var(--nav-portrait-font-size);
    line-height: var(--nav-portrait-font-size);
}
.nav-drawer .nav-link, .nav-drawer .nav-icon-wrapper {
    display: block;
}

.nav-drawer .nav-link:hover, .nav-accordion h3:hover {
    color: var(--theme-color-1-light);
}

.nav-accordion-content {
    display: flex;
    flex-direction: column;
    
    padding-left: 1rem;
}
.nav-drawer .nav-login-buttons {
    justify-content: start;
    padding: 0px;
    width: 100%;
}
.nav-drawer .nav-login-buttons, .nav-drawer > .nav-link {
    border-bottom: solid orange 3px;
}
.nav-accordion h3 {
    border-bottom: solid var(--nav-hover-color) 3px;
}
.nav-accordion .nav-accordion h3 {
    border-bottom: solid var(--nav-hover-color) 1px;    /* Smaller if inside another */
}

/* Spacing */
.nav-accordion h3, .nav-drawer > a, .nav-drawer .nav-login-buttons {
    margin-top: 0.75rem;
    margin-bottom: 1.25rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
}
.nav-drawer a, .nav-drawer .nav-icon-wrapper {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
}



.subnav--ls-inactive {
    display: none;
}
.subnav--ls-active {}





.subnav-section {
    max-width: var(--subnav-section-max-width);
    min-width: var(--subnav-section-min-width);
}



.subnav-title-underline {
    width: 100%;
    height: 2px;
    background-color: var(--nav-hover-color);
}



.subnav ul {
    margin: 0px;
    padding: 0px;

    margin-top: 12px;
}
.subnav li {
    list-style: none;
}
.subnav li:not(:last-child) {
    margin-bottom: 7px;
}
.subnav li a {
    display: block;                 /* For larger hover area */
    width: 100%;

    color: rgb(167, 167, 167);
    font-family: HomeFont;
    font-size: var(--nav-font-size);

    padding-left: 0px;
    
}
.subnav li a.premium {
    /* color: white !important; */
}
.subnav li a:hover {
    color: var(--nav-hover-color) !important;
    padding-left: 5px;
}
@media (orientation: portrait) {
    .subnav li a {
        transition-property: none;
    }
    .subnav li a:hover {
        padding-left: 0px;
    }
}





.subnav-item {
    font-family: HomeFont;
    font-size: var(--nav-font-size);

    line-height: var(--subnav-height);   /* Ensures text is centered vertically */
    text-decoration: none;
    text-align: center;
    vertical-align: middle;
    color: white;
  
    height: 100%;
    z-index: var(--nav-z-item) !important;
    display: inline-block;            /* Ensures correct height as 100% of parent */
    position: relative;               /* For dropdown */
}
.subnav-item a {
    display: inline-block;
    color: white;
    height: 100%;
    width: 100%;
}


@media (orientation: portrait) {
    .nav-logo-div-wrapper {
        display: none;
    }
}
.nav-logo-div {
    position: absolute;
    
    top: calc(var(--nav-height) * 0.45 / 2);
    left: 20px;
    
    height: calc(var(--nav-height) * 0.45);
    /* width: calc(var(--nav-height) * 0.65); */
}
.nav-logo-div img {
    display: block;
    height: 100%;
    width: 100%;
}
.nav-logo-version {
    position: absolute;
    width: 62px;
    bottom: 10px;
    left: 20px;
    
    font-size: 10px;
    text-align: center;
    color: gray;
}
.nav-logo-version:hover {
    color: white;
}



@media (orientation: landscape) {
    .burger-icon {
        display: none;
    }
}


@media (orientation: portrait), (max-width: 900px)  {
    :root {
        --nav-height: 60px;
        --nav-item-padding: 7vw;
        --nav-background-color: rgba(12, 15, 17, 1);
    
        --subnav-height: 50px;
        --subnav-item-padding-left: 30px;
        --subnav-item-padding-right: 15px;
        --subnav-background-color: rgba(15, 15, 15, 1);

        --subnav-section-max-width: 9999px;
        --subnav-section-min-width: 0px;
    
        --nav-z-mega-dropdown: 999;
        --nav-z-item: 9999;
        --undernav-padding: 15px;
        --undernav-height: calc(var(--banner-3-height) + var(--undernav-padding) * 2);
    }
    

    /* Hide landscape nav */
    .nav-landscape {
        display: none;
    }

    /* ---------- Burger ---------- */
    .burger-icon {
        position: absolute;
    
        height: var(--nav-height);
        width: var(--nav-height);
        
        top: 0px;
        right: 0px;
    }
    .burger-icon img {
        display: block;
        height: 100%;
        width: 100%;
    }
    .burger-icon img:hover {
        cursor: pointer;
        opacity: 0.8;
    }
    .burger-icon {
        display: block;
    }

}
