:root {
    --page-h3-margin-left: calc(
        1px
        + 2px
        + 0.5rem
    )
}
.page-h3 {
    border: solid var(--theme-color-1-darker) 1px;
    padding: 2px;

    width: 100%;
    
    margin-bottom: 0.5rem;
}
.page-h3.with-margin {
    margin-left: calc(var(--page-h3-margin-left) * -1);
    width: calc(var(--page-h3-margin-left) + 100%);
}
.page-h3 h3 {
    font-size: var(--h3-size);
    font-family: TextFont;
    font-weight: normal;
    color: white;
    
    background-color: var(--theme-color-1-darker);
    /* background: linear-gradient(to right, var(--theme-color-1-darker), rgb()); */
    
    /* width: 100%; */
    padding: 0.5rem;
    margin: 0px;
}
.page-h3.with-margin h3 {
    
}
.underline {
    margin-top: -6px;
    width: 100%;
    height: 1px;
    background-color: var(--theme-color-1-darker);
    margin-bottom: 0.5rem;
}