
.plus-minus {
    --size: 5rem;
    --margin: calc((var(--stat-selector-size) - var(--size)) / 2);

    padding: 2px;
    
    height: var(--size);
    width: var(--size);
    margin-top: var(--margin);

    cursor: pointer;
}
@media (max-width: 900px) {
    .plus-minus {
        --size: 3rem;
    }
}
.plus-minus > div {
    height: 100%;
    width: 100%;

    text-align: center;
    font-size: 2rem;
    line-height: calc(var(--size) - 0.5rem);
    font-family: HomeFont;
    color: var(--theme-color-1);

    border: solid gray 1px;
    border-radius: 4px;
}