:root {
    --page-h2-margin-left: calc(
        1px
        + 2px
        + 0.5rem
    )
}

.page-h2 {
    margin-top: calc(var(--element-padding) * 2);
    margin-bottom: var(--element-padding);

    position: relative;
    z-index: 1;

    width: 100%;

    padding: 2px;
    border: solid black 1px;
}
.page-h2.with-margin {
    margin-left: calc(var(--page-h2-margin-left) * -1);
    width: calc(var(--page-h2-margin-left) + 100%);
}

