    margin: 0px;
    font-weight: bold;
    font-size: var(--h2-size);
    font-family: HomeFont;
    color: var(--theme-color-1-darker);
} */

.page-h2 h2 {
    width: 100%;
    background-color: var(--dark-gray-color);
    color: white;
    font-family: HomeFont;
    font-weight: normal;
    padding: 0.5rem;
    margin: 0px;
}
.page-h2--underline {
    width: 55%;
    height: 0.65vh;
    background: var(--theme-color-1);
    background: linear-gradient(90deg, var(--theme-color-1-darker) 0%, var(--theme-color-1-darker) 70%, rgba(255,0,0,0) 100%);
}

@media (orientation: portrait), (max-width: 900px)  {
    .page-h2--underline {
        height: 0.75vh;
    }
}